/* overall breathing room */
.brokerage { 
  padding-top: 40px !important; 
  padding-bottom: 40px !important; 
}

/* pretty tabs */
.brokerage .brokerage-tab {
  background: #f5f7fb !important;
  color: #495057 !important;
  border: 0 !important;
  transition: all .2s ease !important;
}
.brokerage .brokerage-tab:hover { 
  background: #eef3ff !important; 
}
.brokerage .brokerage-tab.active {
  background: #e7f0ff !important;
  color: #0d6efd !important;
  box-shadow: 0 6px 16px rgba(13,110,253,.15) !important;
}

/* card-like table container */
.brokerage .brokerage-card {
  background: #fff !important;
  border: 1px solid #e9ecef !important;
  border-radius: 12px !important;
  box-shadow: 0 8px 22px rgba(0,0,0,.05) !important;
  padding: 8px !important;
}

/* table look */
.brokerage table { 
  width: 100% !important; 
  border-collapse: separate !important; 
  border-spacing: 0 !important; 
}
.brokerage thead th {
  background: #f8f9fa !important;
  color: #6c757d !important;
  font-weight: 600 !important;
  text-align: left !important;
  padding: 14px 18px !important;
  border-bottom: 1px solid #e9ecef !important;
}
.brokerage tbody td { 
  padding: 16px 18px !important; 
  vertical-align: middle !important; 
}
.brokerage tbody tr + tr td { 
  border-top: 1px solid #f1f3f5 !important; 
}

/* zebra row */
.brokerage .brokerage-zebra td { 
  background: #fcfcfd !important; 
}

/* first column styles */
.brokerage .brokerage-firstcol { 
  width: 220px !important; 
}
.brokerage .brokerage-firstcol-text { 
  color: #495057 !important; 
  white-space: nowrap !important; 
}

/* Brand color: Zerodha blue */
:root {
  --brand-primary: #387ed1 !important;
}

.link-primary { 
  color: var(--brand-primary) !important; 
}
.link-primary:hover { 
  text-decoration: underline !important; 
}

/* Card look for accordion items */
.support-card {
  background: #fff !important;
  border: 1px solid #e9ecef !important;
  border-radius: .5rem !important;
  margin-bottom: .75rem !important;
  box-shadow: 0 1px 2px rgba(16,24,40,.04) !important;
  overflow: hidden !important;
}

/* Accordion header button */
.support-acc-btn {
  padding: .95rem 1rem !important;
  font-weight: 600 !important;
  background: #fff !important;
}

/* Simple +/− icon at left of header */
.acc-icon {
  display: inline-flex !important;
  align-items: center !important;
  justify-content: center !important;
  width: 22px !important; 
  height: 22px !important;
  border-radius: 50% !important;
  border: 1px solid #d0d5dd !important;
  line-height: 1 !important;
  font-weight: 700 !important;
  color: #64748b !important;
  background: #f8fafc !important;
}

/* Rotate + to − when open (optional visual) */
.accordion-button:not(.collapsed) .acc-icon {
  content: "–" !important;
}
.accordion-button:not(.collapsed) .acc-icon::before {
  content: "–" !important;
}
.accordion-button.collapsed .acc-icon::before {
  content: "+" !important;
}

/* List inside accordion body */
.support-list {
  list-style-type: disc !important;
  margin: 0 !important;
  padding-left: 1.25rem !important;
}
.support-list li { 
  margin-bottom: .5rem !important; 
}

/* Notice panel (soft orange with left strip) */
.notice-box {
  background: #fff7e6 !important;
  border: 1px solid #ffe2b8 !important;
  border-left: 4px solid #f0ad4e !important;
  border-radius: .5rem !important;
  box-shadow: 0 1px 2px rgba(16,24,40,.04) !important;
}

/* Quick links card */
.quick-links-box {
  background: #fff !important;
  border: 1px solid #e9ecef !important;
  border-radius: .5rem !important;
  box-shadow: 0 1px 2px rgba(16,24,40,.04) !important;
}


/* mobile tweaks */
@media (max-width: 768px) {
  .brokerage .brokerage-card { 
    padding: 6px !important; 
  }
  .brokerage thead th, 
  .brokerage tbody td { 
    padding: 12px 14px !important; 
  }
  .brokerage .brokerage-firstcol { 
    width: auto !important; 
  }
}

@media (max-width: 1415px) {
    .signup_btn{
        font-size: 90% !important;
    }

}

@media (max-width: 994px) {
    .signup_btn{
        font-size: 60% !important;
    }
}

/* Team.css */
.team-photo {
  width: 260px;
  height: 260px;
  object-fit: cover;
}

@media (max-width: 992px) {
  .container, .container-fluid { padding-left: 20px !important; padding-right: 20px !important; }
}


@media (max-width: 992px) {


  .about_hero_section{
    display: flex !important;
    flex-direction: column !important;
    width: 100% !important;
  }
  
  .about_hero_section_1{
     width: 100% !important;
  }

  .team-photo {
    width: 200px !important;
    height: 200px !important;
    object-fit: cover !important;
  }

  /* Section container */
  .team_section {
    background-color: #fff !important; /* or aqua if you want */
    display: flex !important;
    flex-direction: column !important;
    justify-content: center !important;
    align-items: center !important;
    text-align: center !important;
    padding: 1rem !important;
  }

  /* Text paragraphs */
  .team_section p {
    font-size: 0.95rem !important;
    line-height: 1.6 !important;
    margin-bottom: 1rem !important;
    text-align: center !important;
  }
}
